.todoItem-appear{
    opacity: 0;
    -webkit-transform: translate(-1rem,0);
            transform: translate(-1rem,0);
    -webkit-transform: translate3d(-1rem,0,0);
            transform: translate3d(-1rem,0,0);
}

.todoItem-appear.todoItem-appear-active{
    opacity: 1;
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    transition-property: opacity,-webkit-transform;
    transition-property: transform,opacity;
    transition-property: transform,opacity,-webkit-transform;
    transition-duration: 300ms;
    transition-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715), linear;
}

.todoItemBtns-enter{
    opacity: 0;
    -webkit-transform: translate(2.5rem,0);
            transform: translate(2.5rem,0);
    -webkit-transform: translate3d(2.5rem,0,0);
            transform: translate3d(2.5rem,0,0);
}

.todoItemBtns-enter.todoItemBtns-enter-active{
    opacity: 1;
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    transition-property: opacity,-webkit-transform;
    transition-property: transform,opacity;
    transition-property: transform,opacity,-webkit-transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715),linear;
}

.todoItemBtns-leave{
    opacity: 1;
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
}

.todoItemBtns-leave.todoItemBtns-leave-active{
    opacity: 0;
    -webkit-transform: translate(2.5rem,0);
            transform: translate(2.5rem,0);
    -webkit-transform: translate3d(2.5rem,0,0);
            transform: translate3d(2.5rem,0,0);
    transition-property: opacity,-webkit-transform;
    transition-property: opacity,transform;
    transition-property: opacity,transform,-webkit-transform;
    transition-duration: 300ms;
    transition-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1),linear;
}


