/***********
  全局样式
************/ 

/*************
    全局变量
**************/ 

/* 字体 */ 

/* 盒模型 */ 

/* 颜色 */ 

/* z-index */ 

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td{
    margin:0;
    padding:0;
    border: 0 none;
    font-size: inherit;
    color: inherit;
    vertical-align: baseline;

    max-height: 100rem; 
} 

h1, h2, h3, h4, h5, h6{
    font-weight: normal;
} 

em, strong{
    font-style: normal;
} 

ul, ol, li{
    list-style: none;
} 

*{
    box-sizing: border-box
} 

*:after, *:before{
    box-sizing: border-box; 
} 

html, body{
    -webkit-tap-highlight-color: transparent;   
} 

body{
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
    font-size: 0.24rem; 
    line-height: 1.5;
    color: #333;
    background: #f2f2f2;
} 

a{
    text-decoration: none;
    outline: none
} 

a:link, a:hover, a:focus{
    text-decoration:none; 
} 

.img{
    max-width: 100%;
} 

.img-responsive{
    display: block;
    width: 100%;
    height: auto;
} 

.text-overflow{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
} 

.text-overflow-1{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-line-clamp: 1;
} 

.text-overflow-2{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-line-clamp: 2;
} 

.wrapper{
    max-width: 10rem;
    margin-left: auto;
    margin-right: auto;
} 

.alert-bg{
    display: none;
    position: fixed;
    width: 100%;
    top: 0;
    bottom:0;
    z-index: 50;
    background: rgba(0,0,0,.6);
} 

.alpha-bg{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background:rgba(0,0,0,.7);
} 

.fixed-bottom{
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 99;
} 

